import React from 'react';
import { Link } from 'react-router-dom';
import { History } from 'history';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSearch } from '@fortawesome/free-solid-svg-icons';
import { useAppSelector } from '../../hooks';

import '../../styles/header.scss';
import logo from '../../logo.png';

type HeaderType = {
	search: string;
	history: History;
	nickname: string;
	setNickname: (x: string) => void;
	setCurrentUrl: (x: string | null) => void;
	setShowMatches: (x: boolean) => void;
	setGlobalFetching: (x: boolean) => void;
};

const Header = ({
	search,
	history,
	nickname,
	setNickname,
	setCurrentUrl,
	setShowMatches,
	setGlobalFetching,
}: HeaderType): JSX.Element => {
	const { isFetching } = useAppSelector(store => store.playerSearch);
	const { isFetching: matchFetching } = useAppSelector(store => store.matchesList);

	const onClickHandler = (e: React.SyntheticEvent) => {
		e.preventDefault();

		setGlobalFetching(true);

		history.push({
			pathname: '/',
			search: `?nickname=${nickname}`
		});     

		const nicknamePURL = new URLSearchParams(search);
		const currentNick = nicknamePURL.get('nickname');

		if (currentNick !== '') {
			setShowMatches(true);
			setCurrentUrl(currentNick)
		}

		setNickname('');
	}    

	const onChangeHandler = (e: React.ChangeEvent<HTMLInputElement>) => {
		setNickname(e.target.value);
	} 

	return (
		<header className="header">
			<div className="header__container">
				<Link to="/home" className="header__logo">
					<img src={logo} alt="" className="header__logo" />
				</Link>               
				<form className="header__form" onSubmit={onClickHandler}>
					<input
						autoComplete="off"
						autoCorrect="off"
						spellCheck="false"
						type="text" 
						value={nickname}
						className="header__input" 
						placeholder="Enter nickname..."
						onChange={onChangeHandler} 
					/>
					<button 
						disabled={isFetching || matchFetching || nickname === ''}
						type="submit"
						className="header__btn-search btn"
					>
						<FontAwesomeIcon icon={faSearch} />
					</button>                        
				</form>
			</div>
		</header>
	);
}

export default Header;
