import React, { useState } from 'react';
import { useHistory, useLocation } from 'react-router-dom';
import { Location, History } from 'history';

import Footer from './components/Footer';
import Header from './components/Header';
import AppRouter from './route/AppRouter';

import './App.scss';

const App = (): JSX.Element => {
	const { search } = useLocation<Location>();
	const history = useHistory<History>();

	const [nickname, setNickname] = useState<string>('');
	const [currentUrl, setCurrentUrl] = useState<string | null>('');
	const [showMatches, setShowMatches] = useState<boolean>(() => {
		return !history.location.pathname.includes('/match/');
	});
	const [globalFetching, setGlobalFetching] = useState<boolean>(false);
	
	return (
		<>
			<Header 
				search={search}
				history={history}
				nickname={nickname}
				setNickname={setNickname}
				setCurrentUrl={setCurrentUrl}
				setShowMatches={setShowMatches}
				setGlobalFetching={setGlobalFetching}
			/>
			<AppRouter 
				search={search}
				currentUrl={currentUrl}
				setCurrentUrl={setCurrentUrl}
				showMatches={showMatches}
				setShowMatches={setShowMatches}
				globalFetching={globalFetching}
				setGlobalFetching={setGlobalFetching}
				history={history}
			/>
			<Footer />
		</>
	);
}

export default App;
