import { ThunkAction } from 'redux-thunk';

import { PlayerStatsType, PlayerInfoType } from './../../types/types';
import API from '../../api/api';
import agent from '../../api/instance_v4';
import { RootState } from '../redux-store';
import {
	–°LEAR_STATE,
	GET_SEARCH_RESULT_SUCCESS,
	GET_SEARCH_RESULT_REQUEST,
	GET_SEARCH_RESULT_FAILURE,
	GET_COMPARISON_LIST_REQUEST,
	GET_COMPARISON_LIST_SUCCESS,
	GET_COMPARISON_LIST_FAILURE
} from './consts';

type ThunkResult = ThunkAction<Promise<void>, RootState, unknown, Actions>;

export type Actions = ClearStateType
	| GetSearchResultRequestType
	| GetSearchResultSuccessType
	| GetSearchResultFailureType
	| SetSecondPlayersRequestType
	| SetSecondPlayersSuccessType
	| SetSecondPlayersFailureType;

type ClearStateType = {
	type: '–°LEAR_STATE';
};
type GetSearchResultRequestType = {
	type: 'GET_SEARCH_RESULT_REQUEST';
};
type GetSearchResultSuccessType = {
	type: 'GET_SEARCH_RESULT_SUCCESS';
	playerStats: PlayerStatsType;
	playerInfo: PlayerInfoType;
};
type GetSearchResultFailureType = {
	type: 'GET_SEARCH_RESULT_FAILURE';
	error: any;
} ;
type SetSecondPlayersRequestType = {
	type: 'GET_COMPARISON_LIST_REQUEST';
};
type SetSecondPlayersSuccessType = {
	type: 'GET_COMPARISON_LIST_SUCCESS';
	secondPlayerStats: PlayerStatsType;
	secondPlayerInfo: PlayerInfoType;
};
type SetSecondPlayersFailureType = {
	type: 'GET_COMPARISON_LIST_FAILURE';
	secError: any;
};

export const clearState = (): ClearStateType => ({ type: –°LEAR_STATE });

/** GET SEARCH RESULT */
export const getSearchResultRequest = (): GetSearchResultRequestType => ({ type: GET_SEARCH_RESULT_REQUEST });
export const getSearchResultSuccess = (playerStats: PlayerStatsType, playerInfo: PlayerInfoType): GetSearchResultSuccessType => ({
	type: GET_SEARCH_RESULT_SUCCESS,
	playerStats,
	playerInfo
});
export const getSearchResultFailure = (error: any): GetSearchResultFailureType => ({
	type: GET_SEARCH_RESULT_FAILURE,
	error,
});

export const getSearchResultThunkCreator = (nickname: string | null): ThunkResult => {
	return async (dispatch) => {
		dispatch(getSearchResultRequest());

		try {
			const getUserIdBySearch = await API.GET(`search/v1?limit=3&query=${nickname}`);
			const { guid: userId, nickname: userNickname } = getUserIdBySearch.data.payload.players.results[0];

			const searchResponse = await agent.get('/players', { params: { nickname: userNickname, game: 'csgo' } });
				
			const statResponse = await API.GET(`stats/v1/stats/users/${userId}/games/csgo`);
			const playerStats = searchResponse.data;

			const playerInfo = {
				playerAvatar: playerStats.avatar,
				skill_level: playerStats.games.csgo.skill_level,
				faceit_elo: playerStats.games.csgo.faceit_elo,
				nickname: userNickname,
				playerId: userId
			};

			dispatch(getSearchResultSuccess(statResponse.data.lifetime, playerInfo));
		} catch (error) {
			dispatch(getSearchResultFailure(error));
		}
	};
};

/** SET SECOND PLAYER */
export const setSecondPlayersRequest = (): SetSecondPlayersRequestType => ({ type: GET_COMPARISON_LIST_REQUEST });
export const setSecondPlayersSuccess = (secondPlayerStats: PlayerStatsType, secondPlayerInfo: PlayerInfoType): SetSecondPlayersSuccessType => ({
	type: GET_COMPARISON_LIST_SUCCESS,
	secondPlayerStats,
	secondPlayerInfo
});
export const setSecondPlayersFailure = (secError: any): SetSecondPlayersFailureType => ({
	type: GET_COMPARISON_LIST_FAILURE,
	secError
});

export const setSecondPlayerThunkCreator = (nickname: string): ThunkResult => {
	return async (dispatch) => {
		dispatch(setSecondPlayersRequest());

		try {			
			const getUserIdBySearch = await API.GET(`search/v1?limit=3&query=${nickname}`);
			const { guid: userId, nickname: userNickname } = getUserIdBySearch.data.payload.players.results[0];

			const searchResponse = await agent.get('/players', { params: { nickname: userNickname, game: 'csgo' } });
			const playerStats = searchResponse.data;

			const playerInfo = {
				playerAvatar: playerStats.avatar,
				skill_level: playerStats.games.csgo.skill_level,
				faceit_elo: playerStats.games.csgo.faceit_elo,
				nickname: userNickname,
				playerId: userId
			};
			const statResponse = await API.GET(`stats/v1/stats/users/${userId}/games/csgo`);

			dispatch(setSecondPlayersSuccess(statResponse.data.lifetime, playerInfo));

		} catch (error) {
			dispatch(setSecondPlayersFailure(error));
		}
	};
};
