import { PlayerStatsType, PlayerInfoType } from '../../types/types';
import { Actions } from './actions';
import {
  –°LEAR_STATE,
  GET_SEARCH_RESULT_SUCCESS,
  GET_SEARCH_RESULT_REQUEST,
	GET_SEARCH_RESULT_FAILURE,
	GET_COMPARISON_LIST_REQUEST,
	GET_COMPARISON_LIST_SUCCESS,
	GET_COMPARISON_LIST_FAILURE
} from "./consts";
  
export type InitialStateType = {
	playerStats: PlayerStatsType;
	playerInfo: PlayerInfoType,
	secondPlayerStats: PlayerStatsType;
	secondPlayerInfo: PlayerInfoType,
	isFetching: boolean;
	secondPlayerIsFetching: boolean;
	error: any;
	secError: any;
};

const initialState: InitialStateType = {
	playerStats: {
		m1: '',
		k5: '',
		k6: '',
		k8: '',
	},
	playerInfo: {
		playerAvatar: '',
		skill_level: null,
		faceit_elo: null,
		nickname: '',
		playerId: null,
	},
	secondPlayerStats: {
		m1: '',
		k5: '',
		k6: '',
		k8: '',
	},
	secondPlayerInfo: {
		playerAvatar: '',
		skill_level: null,
		faceit_elo: null,
		nickname: '',
		playerId: null,
	},
	isFetching: false,
	secondPlayerIsFetching: false,
	error: null,
	secError: null,
};
  
const playerReducer = (state = initialState, action: Actions): InitialStateType => {
	switch (action.type) {
		case –°LEAR_STATE:
			return {
				...state,
				playerStats: {
					m1: '',
					k5: '',
					k6: '',
					k8: '',
				},
				playerInfo: {
					playerAvatar: null,
					skill_level: null,
					faceit_elo: null,
					nickname: '',
					playerId: null
				}
			};
		case GET_SEARCH_RESULT_REQUEST:
			return {
				...state,
				playerStats: {
					m1: '',
					k5: '',
					k6: '',
					k8: '',
				},
				playerInfo: {
					...state.playerInfo,
					nickname: '', 
					playerId: null
				},
				secondPlayerStats: {
					m1: '',
					k5: '',
					k6: '',
					k8: '',
				},
				secondPlayerInfo: {
					...state.playerInfo,
					nickname: '', 
					playerId: null
				},
				isFetching: true,
				error: null
			};
		case GET_SEARCH_RESULT_SUCCESS:
			return {
				...state,
				playerStats: action.playerStats,
				playerInfo: action.playerInfo,
				error: null,
				isFetching: false
			};		
		case GET_SEARCH_RESULT_FAILURE:
			return {
				...state,
				error: action.error,
				isFetching: false,
			};
		case GET_COMPARISON_LIST_REQUEST:
			return {
				...state,
				secondPlayerStats: {
					m1: '',
					k5: '',
					k6: '',
					k8: '',
				},
				secondPlayerInfo: {
					...state.playerInfo,
					nickname: '', 
					playerId: null
				},
				secondPlayerIsFetching: true,
				secError: null
			};
		case GET_COMPARISON_LIST_SUCCESS:
			return {
				...state,
				secondPlayerStats: action.secondPlayerStats,
				secondPlayerInfo: action.secondPlayerInfo,
				secondPlayerIsFetching: false,
				secError: null
			};
		case GET_COMPARISON_LIST_FAILURE:
			return {
				...state,
				secError: action.secError,
				secondPlayerIsFetching: false,
			};
		default:
			return state;
	}
};
  
  export default playerReducer;
  