import { ThunkAction } from 'redux-thunk';

import API from '../../api/api';
import { RootState } from '../redux-store';
import {
  GET_ROOM_REQUEST,
  GET_ROOM_SUCCESS,
  GET_ROOM_FAILURE
} from './consts';

export type Actions = GetTeamsRequestType
| GetTeamsSuccessType
| GetTeamsFailureType;

type ThunkResult = ThunkAction<Promise<void>, RootState, unknown, Actions>;

type GetTeamsRequestType = { type: 'GET_ROOM_REQUEST' };
type GetTeamsSuccessType = {
		type: 'GET_ROOM_SUCCESS';
		teamsInfo: any;
		teamsStats: any;
	};
type GetTeamsFailureType = {
		type: 'GET_ROOM_FAILURE';
		error: any;
	};


/** GET MATCHES LIST */
export const getTeamsRequest = (): GetTeamsRequestType => ({ type: GET_ROOM_REQUEST });
export const getTeamsSuccess = (teamsInfo: any, teamsStats: any): GetTeamsSuccessType => ({
	type: GET_ROOM_SUCCESS,
	teamsInfo,
	teamsStats
});
export const getTeamsFailure = (error: any): GetTeamsFailureType => ({
	type: GET_ROOM_FAILURE,
	error,
});

export const getTeamsThunkCreator = (roomId: string): ThunkResult => {
	return async (dispatch) => {
		dispatch(getTeamsRequest());

		try {
			const teamsResponse = await API.GET(`match/v2/match/${roomId}`);
			const teamsStatsResponse = await API.GET(`/stats/v1/stats/matches/${roomId}`);

			dispatch(getTeamsSuccess(teamsResponse.data.payload, teamsStatsResponse.data[0]));
		} catch (error) {
			dispatch(getTeamsFailure(error));
		}
	};
};
