import * as axios from "axios";

export const API_URL = 'https://open.faceit.com/data/v4';
export const API_KEY = 'bed7e688-9b35-4378-a761-a8acd33d160f';

const agent = axios.create({
  baseURL: API_URL,
  headers: {
    'content-type': 'application/json',
    Authorization: `Bearer ${API_KEY}`
  },
});

export default agent;
