import { ThunkAction } from 'redux-thunk';

import API from '../../api/api';
import { RootState } from '../redux-store';
import {
  GET_MATCHES_LIST_REQUEST,
  GET_MATCHES_LIST_SUCCESS,
	GET_MATCHES_LIST_FAILURE,
	GET_SEC_MATCHES_LIST_REQUEST,
	GET_SEC_MATCHES_LIST_SUCCESS,
	GET_SEC_MATCHES_LIST_FAILURE,
	CLEAR_MATCHES
} from './consts';

type ThunkResult = ThunkAction<Promise<void>, RootState, unknown, Actions>;

export type Actions = ClearMatchesType
	| GetMatchesRequestType
	| GetMatchesSuccessType
	| GetMatchesFailureType
	| GetSecMatchesRequestType
	| GetSecMatchesSuccessType
	| GetSecMatchesFailureType;

type ClearMatchesType = { type: 'CLEAR_MATCHES' };
type GetMatchesRequestType = { type: 'GET_MATCHES_LIST_REQUEST' };
type GetMatchesSuccessType = {
  type: 'GET_MATCHES_LIST_SUCCESS';
  allMatches: any;
};
type GetMatchesFailureType = {
  type: 'GET_MATCHES_LIST_FAILURE';
  error: any;
}
type GetSecMatchesRequestType = {
  type: 'GET_SEC_MATCHES_LIST_REQUEST';
}
type GetSecMatchesSuccessType = {
  type: 'GET_SEC_MATCHES_LIST_SUCCESS';
  secMatches: any;
}
type GetSecMatchesFailureType = {
  type: 'GET_SEC_MATCHES_LIST_FAILURE';
  error: any;
}

export const clearMatches = (): ClearMatchesType => ({ type: CLEAR_MATCHES });

/** GET MATCHES LIST */
export const getMatchesRequest = (): GetMatchesRequestType => ({ type: GET_MATCHES_LIST_REQUEST });
export const getMatchesSuccess = (allMatches: any): GetMatchesSuccessType => ({
	type: GET_MATCHES_LIST_SUCCESS,
	allMatches,
});
export const getMatchesFailure = (error: any): GetMatchesFailureType => ({
	type: GET_MATCHES_LIST_FAILURE,
	error,
});

export const getMatchesThunkCreator = (playerId: string | null, matchesCount: string): ThunkResult => {
	return async (dispatch) => {
		dispatch(getMatchesRequest());

		try {
			const allMatchesResopnse = await API.GET(`stats/api/v1/stats/time/users/${playerId}/games/csgo?size=${matchesCount}`);

			dispatch(getMatchesSuccess(allMatchesResopnse.data));
		} catch (error) {
			dispatch(getMatchesFailure(error));
		}
	};
};

/** GET SECONDS PLAYER MATCHES LIST */
export const getSecMatchesRequest = (): GetSecMatchesRequestType => ({ type: GET_SEC_MATCHES_LIST_REQUEST });
export const getSecMatchesSuccess = (secMatches: any): GetSecMatchesSuccessType => ({
	type: GET_SEC_MATCHES_LIST_SUCCESS,
	secMatches,
});
export const getSecMatchesFailure = (error: any): GetSecMatchesFailureType => ({
	type: GET_SEC_MATCHES_LIST_FAILURE,
	error,
});

export const getSecMatchesThunkCreator = (playerId: string | null, matchesCount: string): ThunkResult => {
	return async (dispatch) => {
		dispatch(getSecMatchesRequest());

		try {
			const allMatchesResopnse = await API.GET(`stats/api/v1/stats/time/users/${playerId}/games/csgo?size=${matchesCount}`);

			dispatch(getSecMatchesSuccess(allMatchesResopnse.data));
		} catch (error) {
			dispatch(getSecMatchesFailure(error));
		}
	};
};
