import React from 'react';
import { Switch, Route } from 'react-router-dom';
import { History } from 'history';

import Preview from '../pages/Preview';
import Main from '../pages/Main';

type AppRouterType = {
	search: string;
	currentUrl: string | null;
	setCurrentUrl: (x: string | null) => void;
	showMatches: boolean;
	setShowMatches: (x: boolean) => void;
	globalFetching: boolean;
	setGlobalFetching: (x: boolean) => void;
	history: History;
};

const AppRouter = ({
	search,
	currentUrl,
	setCurrentUrl,
	showMatches,
	setShowMatches,
	globalFetching,
	setGlobalFetching,
	history,
}: AppRouterType): JSX.Element => {
	return (
		<Switch>
			<Route path="/">
				{search
					? <Main 
						search={search}
						currentUrl={currentUrl}
						setCurrentUrl={setCurrentUrl}
						showMatches={showMatches}
						setShowMatches={setShowMatches}
						setGlobalFetching={setGlobalFetching}
						globalFetching={globalFetching}
						history={history}
					/>  
					: <Preview />
				}
			</Route>
			<Route exact path="/home" component={Preview} />
		</Switch>
	);
}

export default AppRouter;
