import { Actions } from './actions';
import {
	GET_ROOM_REQUEST,
	GET_ROOM_SUCCESS,
	GET_ROOM_FAILURE,
} from './consts';

export type InitialStateType = {
	teamsInfo: any;
	teamsStats: any;
	isFetching: boolean;
	error: any; 
};

const initialState: InitialStateType = {
	teamsInfo: null,
	teamsStats: null,
	isFetching: false,
	error: null
};

const roomReducer = (state: InitialStateType = initialState, action: Actions): InitialStateType => {
	switch (action.type) {
		case GET_ROOM_REQUEST:
			return {
				...state,
				teamsInfo: null,
				teamsStats: null,
				isFetching: true,
				error: null
			};
		case GET_ROOM_SUCCESS:
			return {
				...state,
				teamsInfo: action.teamsInfo,
				teamsStats: action.teamsStats,
				error: null,
				isFetching: false
			};
		case GET_ROOM_FAILURE:
			return {
				...state,
				error: action.error,
				isFetching: false,
			};
		default:
			return state;
	}
};
  
export default roomReducer;
  